.wrapper {
    width: 165px;
    .rating {
        display: inline-block;
        unicode-bidi: bidi-override;
        color: #969696;
        font-size: 17px;
        height: 17px;
        width: auto;
        margin: 0;
        position: relative;
        padding: 0;
        .rating-upper {
            color: #FAE212;
            padding: 0;
            position: absolute;
            z-index: 1;
            display: flex;
            top: 0;
            left: 0;
            overflow: hidden; }
        .rating-lower {
            padding: 0;
            display: flex;
            z-index: 0; } }
    .info {
        span {
            display: block;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            font-family: Montserrat Regular;
            margin-top: 5px;
            &.label {
                font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen';
                font-style: normal;
                font-weight: bold;
                font-size: 20px;
                line-height: 29px;
                color: #4D4E50; }
            &.name {
                font-family: Montserrat Regular;
                font-style: normal;
                font-weight: normal;
                font-size: 18px;
                line-height: 22px;
                color: #969696; }
            &.params {
                font-family: Montserrat Regular;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                color: #836FE0; }
            &.price {
                font-family: Montserrat Regular;
                font-style: normal;
                font-weight: bold;
                font-size: 28px;
                line-height: 34px;
                color: #836FE0; } } } }

